@import url(https://fonts.googleapis.com/css2?family=Mulish&display=swap);
* {
  margin: 0;
  padding: 0;
  font-family: muli, sans-serif;
  box-sizing: border-box;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.nav-link:hover {
  color: rgb(0, 174, 255) !important;
}
.active-menu {
  font-weight: bold;
  border-bottom: 1px solid #565387;
}

.intro {
  font-size: 5rem;
  color: rgb(76, 79, 80);
}
.img {
  width: auto;
  height: 90vh;
}
.typing-text {
  margin-top: 1rem;
  font-size: 2rem;
  font-weight: 500;
}
.button {
  width: auto;
  height: 2.5rem;
}
@media only screen and (max-width: 500px) {
  .img {
    margin: auto;
    width: 15rem;
    height: auto;
    padding-top: 2rem;
  }
  .mainBox {
    position: relative;
  }
  .button {
    position: absolute;
    top: 34rem;
    left: 39%;
  }
}

.message-box {
  border-radius: 1rem;
  box-shadow: 1px 1px 2px #555;
  padding: 2rem;
  background: linear-gradient(239deg, #27c6cec7, #403b3b00);
}

.resume {
  width: 1.8rem;
}
.resume-btn {
  width: 10rem;
}
.my-image {
  width: 22rem;
  height: auto;
}

.background-img {
  position: fixed;
  top: 0;
  bottom: 0;
  width: 100vw;
  height: 100vh;
  z-index: 1;
  background-color: rgba(104, 104, 104, 0.788);
}
.close-btn {
  width: 2.5rem;
  height: auto;
}
.modal-box {
  background-color: rgb(255, 255, 255);
  box-shadow: 0px 7px 17px 2px #716a6ae0;
  border-radius: 1rem;
  position: relative;
}
.heading {
  text-shadow: -2px 1px 6px rgba(139, 139, 139, 0.836);
}

@media only screen and (max-width: 600px) {
  .close-btn {
    position: absolute;
    left: 2px;
    top: -2.2rem;
    width: 0.6rem;
    height: 1.8rem;
  }
  .project-link {
    font-size: 12px;
    margin: auto;
  }
}

.header {
  box-shadow: 1px 1px 3px rgba(151, 149, 149, 0.863);
  margin-bottom: 2px;
}

.carousel {
  width: auto;
  height: 80wh;
}
.carousel-img {
  border-radius: 1rem;
  object-fit: cover;
  width: 70rem;
  height: 33rem;
}

.imageBox {
  height: 25rem;
  box-shadow: 1px 14px 17px black;
}
/*.img-box {
  overflow: hidden;
}*/

.img-box .imageBox {
  transition: -webkit-transform 1.5s, -webkit-filter 1.5s ease-in-out;
  transition: transform 1.5s, filter 1.5s ease-in-out;
  transition: transform 1.5s, filter 1.5s ease-in-out, -webkit-transform 1.5s, -webkit-filter 1.5s ease-in-out;
  -webkit-transform-origin: center center;
          transform-origin: center center;
  -webkit-filter: brightness(50%);
          filter: brightness(50%);
}

.img-box:hover .imageBox {
  -webkit-filter: brightness(100%);
          filter: brightness(100%);
  -webkit-transform: scale(1.15);
          transform: scale(1.15);
}
.content-row {
  transition: -webkit-transform 1.5s, -webkit-filter 1.5s ease-in-out;
  transition: transform 1.5s, filter 1.5s ease-in-out;
  transition: transform 1.5s, filter 1.5s ease-in-out, -webkit-transform 1.5s, -webkit-filter 1.5s ease-in-out;
}
.content-row:hover {
  box-shadow: inset -1px 1px 11px 1px #565454b3;
  background-color: #e7e6e6c4;
}

